.images-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
}


.images-list div {
    width: 33%;
    height: 400px;
    margin-bottom: 10px;    
}

.images-list div img {
    width: 100%;
    height: 100%;
}

@media (max-width:810px) {
    .images-list div {
        width: 100%;
        height: auto;
    }
}